.cwt--animated-offcanvas {
  cursor: pointer;
}

.cwt--offcanvas-area {
  z-index: 999;
  background-color: var(--black-3);
  opacity: 0;
  visibility: hidden;
  overflow: hidden;
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
}

.cwt--animated-offcanvas svg,
.cwt--offcanvas-area svg {
  width: 1em;
  height: 1em;
}

.cwt--offcanvas-inner {
  display: grid;
  grid-template-columns: var(--width-left, auto) var(--width-right, auto);
}

.cwt--offcanvas-inner li {
  list-style: none;
}

.cwt--offcanvas-left {
  height: 100vh;
  padding: 60px 50px 30px;
  background-color: var(--black-2);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  opacity: 0;
  top: -50%;
  position: relative;
}

.cwt--offcanvas-right {
  padding: 40px 0;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-color: var(--black-3);
  opacity: 0;
  bottom: -50%;
  position: relative;
}

.cwt--offcanvas-logo img {
  object-fit: cover;
}

.cwt--offcanvas-contact {
  display: grid;
}

ul.cwt--offcanvas-contact p {
  color: #fff;
}

.cwt--offcanvas-contact li a,
.cwt--offcanvas-contact li span {
  font-weight: 400;
  font-size: 18px;
  line-height: 1.3;
  color: var(--white);
  display: inline-block;
}

.cwt--offcanvas-close {
  width: 80px;
  height: 80px;
  margin-left: 30px;
  margin-right: 30px;
}

.cwt--offcanvas-close .close-btn {
  width: 100%;
  height: 100%;
  border-radius: 100%;
  background: var(--black-6);
  position: relative;
  font-size: 15px;
  font-weight: 500;
  display: inline-flex;
  align-items: center;
  gap: 5px;
  justify-content: center;
  fill: #fff;
  color: #fff;
  transition: all 0.3s;
}

.cwt--offcanvas-close button span {
  width: 30px;
  height: 1px;
  display: inline-block;
  background-color: var(--primary);
  position: absolute;
  left: 50%;
  top: 50%;
}

.cwt--offcanvas-close button span:first-child {
  transform: translateX(-50%) rotate(45deg);
}

.cwt--offcanvas-close button span:last-child {
  transform: translateX(-50%) rotate(-45deg);
}

.cwt--offcanvas-menu li a,
.cwt--offcanvas-menu .dp-menu a {
  font-size: 30px;
  font-weight: 500;
  line-height: 1em;
  text-decoration: none;
  width: 100%;
  padding: 20px 0 20px 60px;
  color: var(--white);
  text-transform: capitalize;
  border-top: 1px solid var(--black-4);
  display: flex;
  justify-content: space-between;
  align-items: center;
  outline: none;
}

.cwt--offcanvas-menu .dp-menu a {
  padding-left: 100px;
}

.cwt--offcanvas-menu .dp-menu .dp-menu a {
  padding-left: 140px;
}

.menu-item-has-children a .nav-direction-icon::after {
  content: attr(data-icon);
  font-size: 40px;
  color: #fff;
  display: block;
  position: absolute;
  right: 30px;
  line-height: 0;
}

.cwt--offcanvas-menu .dp-menu {
  display: none;
}

.cwt--offcanvas-lang {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.cwt--offcanvas-lang .language {
  gap: 30px;
  display: flex;
}

.cwt--offcanvas-lang .language li a {
  font-weight: 500;
  font-size: 16px;
  line-height: 1.5;
  color: var(--white);
  display: inline-block;
  text-transform: capitalize;
  position: relative;
}

.cwt--offcanvas-lang .language li a::after {
  position: absolute;
  content: "";
  width: 100%;
  height: 1px;
  left: 0;
  bottom: 0;
  transition: all 0.3s;
  background-color: var(--white);
}

.cwt--offcanvas-lang .language li a:hover {
  color: var(--primary);
}

.cwt--offcanvas-lang .language li a:hover::after {
  width: 0;
}

.cwt--offcanvas-footer .f-icon {
  fill: var(--white);
  color: var(--white);
  text-decoration: none;
  line-height: 0;
}

.cwt--offcanvas-footer .default-search__again-form form input {
  width: 100%;
  height: 37px;
  padding: 0;
  color: var(--black-2);
}

.cwt--offcanvas-footer form button {
  font-size: 14px;
  color: var(--gray);
}

.cwt--offcanvas-footer {
  gap: 30px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.cwt--offcanvas-footer a:hover {
  color: var(--primary);
}

.cwt--offcanvas-footer form input,
.cwt--offcanvas-footer form textarea {
  width: 100%;
  font-size: 14px;
  color: var(--gray);
  border: none;
  outline: none;
  padding-bottom: 5px;
  background-color: transparent;
  border-bottom: 1px solid var(--gray-6);
  background-repeat: no-repeat;
  background-position: right 0;
}

.cwt--offcanvas-footer form input:hover,
.cwt--offcanvas-footer form textarea:hover {
  opacity: 1;
  color: var(--gray);
}

.cwt--offcanvas-menu {
  width: 99%;
  height: 80vh;
  overflow-y: auto;
}

.cwt--offcanvas-menu .menu {
  width: 100%;
  border-bottom: 1px solid var(--black-4);
}

.cwt--offcanvas-area .has-left-line {
  position: relative;
  padding-inline-start: 37px;
  display: inline-block;
}

.cwt--offcanvas-area .has-left-line:before {
  position: absolute;
  content: "";
  width: 50px;
  height: 1px;
  background-color: currentColor;
  inset-inline-start: 0;
  border-inline-start: 1px;
  top: 50%;
}

.cwt--offcanvas-area .hover-border-move li a {
  position: relative;
  transition: all 0.5s;
}

.cwt--offcanvas-area .hover-border-move li a:hover::after {
  width: 100%;
  left: auto;
  right: 0;
}

.cwt--offcanvas-area .hover-border-move li a::after {
  position: absolute;
  content: "";
  width: 0%;
  height: 1px;
  bottom: 0;
  left: 0;
  transition: all 0.5s;
  background-color: currentColor;
}

.cwt--offcanvas-follow {
  display: flex;
  align-items: center;
  gap: 10px;
}

.cwt--offcanvas-footer form {
  width: 220px;
  border: none;
}


/* Mac */
@media (max-width: 1600px) {
  .cwt--offcanvas-area .cwt--offcanvas-footer {
    flex-wrap: wrap;
    flex-direction: column-reverse;
    align-items: flex-start;
  }

  .cwt--offcanvas-area .default-search__again-form {
    margin: 0;
  }
}

/* Large Tablet */
@media (max-width: 1365px) {
  .cwt--offcanvas-footer {
    align-items: flex-start;
    flex-direction: column-reverse;
  }
}

/* Tablet */
@media (max-width: 1023px) {
  .default-search__again-form {
    width: 100%;
  }

  .cwt--offcanvas-footer form {
    width: 100%;
  }
}

/* Mobile */
@media (max-width: 767px) {
  .cwt--offcanvas-left {
    height: 100%;
  }

  .cwt--offcanvas-right {
    height: 100%;
  }

  .cwt--offcanvas-inner {
    height: 100%;
  }

  .cwt--offcanvas-menu {
    width: 100%;
  }

  .cwt--offcanvas-menu li a::after {
    width: 10px;
    height: 10px;
    left: 65px;
    top: 5px;
  }

  .cwt--offcanvas-menu li a:hover::before {
    width: 30px;
  }

  .cwt--offcanvas-menu li a::before {
    height: 2px;
  }
}